.main-movieDetails {
  width: 100%;
  flex-direction: row-reverse;
  gap: 3;
  .left-section {
    display: flex;
    padding-top: 1rem;
    > div {
      width: 50%;
      height: 100%;
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
    }
  }
  .right-section {
    width: 100%;
    height: 400px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    img{
        height: 100%;
    }
  }
}
@media(max-width : 768px){
    .right-section{
        padding: 2rem 0;
    }
}
