@import '../../common/Color.scss';
.header{
   padding: 20px 0;
   background-color: $background-secondary;
   display: flex;
   align-items: center;
   justify-content: center;
    .container-fluid{
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

    form{
        padding: 0;
        margin: 0;
    }
    input{
        outline: none;
        border: 1px solid $text-cyan;
        border-radius: 5px;
        background-color: $background-secondary;
        color: cyan;
        padding: 3px;
    }
    input::placeholder{
        color: $text-cyan;
        padding: 3px;
    }
    
    button{
        border: none;
        background-color: $background-secondary;
        position: relative;
    }
    .searchIcon{
        color: $text-white;
        position: absolute;
        bottom: -2px;
        right: 5px;
    }

}