@import '../../common/Color.scss';
.card{
    background-color: $background-secondary;
    color: $text-white;
    border: 1px solid $text-cyan;
    padding: .5rem;
    cursor: pointer;
    .card-top{
        width: 100%;
        height: 250px;
        transition: all .5s;
        img{
            height: 100%;
            width: 100%;
            transition: all .3s;

        }
    }
    .card-bottom{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding: 1rem 0;

        p{
            color: $text-cyan;
        }
    }
}
.card:hover > div img {
    transform: scale(1.05);
}
